@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";


.cathay-carosel{
  &.#{$prefix}-layout-video{
    .slick-slider{
      // width: 960px;
      // margin-left: auto;
      // margin-right: auto;
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        width: 960px;
        margin-left: auto;
        margin-right: auto;
      }
    }


    .slick-list{
      padding:0;
    }
    .slick-track{
      padding-top: 25px;
      padding-bottom: 15px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // margin:25px 0 50px;
      }

    }
    .slick-slide{
      border:1px solid #ddd;
      margin:0 10px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;

      box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.14);
   
      display: flex;
      flex-wrap: wrap;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        
        overflow: hidden;
        padding: 0px;
        >.cs-link{
          display: flex;
          flex-wrap: wrap;
        }
      }

      &:hover,&:active{
        >.cs-link{
          // opacity:1;
        }
        .btn{
          color: #fff;
          background-color: #d5780a;
          border-color: #d5780a;
        }
      }
    }
    .#{$prefix}link{
      display: block;
    }
    .#{$prefix}title{
      font-size:19px;
      font-weight: 300;
      line-height: 1.5;
      color:#333333;
      letter-spacing: 2px;
      text-align: left;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // padding-left:30px;
        font-size:28px;
      }
    }
    .#{$prefix}subtitle{
      display: none;
    }
    .#{$prefix}content{
      font-size:16px;
      line-height: 1.5;
      color:#666666;
      font-weight: 400;
      letter-spacing: 1px;
      margin-left:auto;
      margin-right:auto;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        width: 100%;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }


     p{
        font-size:16px;
        font-weight: 400;
      }
      ul{
        list-style: disc;
        padding-left: 17px;
        @media (min-width: map-get($grid-breakpoints, 'md')){
          padding-left: 17px;
        }
      }
      li{
        font-size:16px;
        color:#00a94f;
        font-weight: 400;
      }
    }
    .slick-dots{
      bottom:-15px;
    }

    .#{$prefix}subtitle + .#{$prefix}content{
      margin-top: 10px;
    }


    .#{$prefix}title + .#{$prefix}content{
      margin-top: 10px;
    }

    .#{$prefix}txt-wrap{
      display: block;
      align-self: center;
      order:30;
      display: block;
      padding:18px;
      position: relative;
      width: 100%;
      margin:0 auto;


      @media (min-width: map-get($grid-breakpoints, 'md')){
        flex-basis: 50%;
        width: auto;
        margin:0;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        flex-basis:34%;
      }
    }

    .#{$prefix}img-wrap{
      order:20;
      height: 213px;
      width: 100%;
      background-size:cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center;
      vertical-align: middle;
      position: relative;
      img{
        visibility: hidden;
      }
      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        flex-basis: 50%;
        min-height:300px;
        height: auto;
      }


      @media (min-width: map-get($grid-breakpoints, 'lg')){
        flex-basis:66%;
      }
    }

    .#{$prefix}more{
      display: block;
      text-align: center;
      padding-top: 15px;
    }
    .btn{
      // font-size:16px;
      // height: 52px;
      // line-height: 50px;
      // padding: 0px 40px;
      &:hover{
        color:#fff;
      }
    }

    .slick-prev,.slick-next {
      top: calc(50% + 35px);
      @media (min-width: map-get($grid-breakpoints, 'md')){
        top: calc(50% - 20px);
      }
    }
    
    .slick-prev {
      @media (min-width: map-get($grid-breakpoints, 'md')){
        left: 10px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        left: -15px;
      }
      @media (min-width: map-get($grid-breakpoints, 'xl')){
        left: -8%;
      }
    }
    .slick-next {
      @media (min-width: map-get($grid-breakpoints, 'md')){
        right: 10px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        right: -15px;
      }
      @media (min-width: map-get($grid-breakpoints, 'xl')){
        right: -8%;
      }
    }
  }
}